function checkUserPermission(tid, team, members) {
	/*return new Promise((resolve, reject) => {
 	});*/

	if (isMentor()) {
		console.log('in mentor mode');
		document.body.classList.add('mentoring');
		var editableNodes = document.querySelectorAll('[contenteditable=true]');
		for (var n = 0; n < editableNodes.length; n++) {
			editableNodes[n].setAttribute('contenteditable', false);
		}
	}

	var uid = database.getCurrentUser().uid;
	if (uid in members || isMentor()) {
		if (Object.keys(team).length > 0) {
			renderTeamCharter(tid, team, members);
		}
	} else if (params.code) {
		vex.dialog.confirm({
			message: 'Do you want to join ' + (team.name || 'this team') + '?',
			buttons: [$.extend({}, vex.dialog.buttons.YES, { text: 'Yes' }), $.extend({}, vex.dialog.buttons.NO, { text: 'No' })],
			callback: function callback(yes) {
				if (yes) {
					database.joinTeam(tid, uid, params.code).then(function (res) {
						if (res.success) {
							vex.dialog.alert({
								message: 'Congratulations, you just joined ' + (team.name || 'your new team') + '!'
							});
							if (Object.keys(team).length > 0) {
								renderTeamCharter(tid, team, members);
							}
						} else {
							window.location = window.location.origin + '/me.html';
						}
					});
				} else {
					window.location = window.location.origin + '/me.html';
				}
			}
		});
	} else {
		window.location = window.location.origin + '/me.html';
	}
}

function isMentor() {
	return MENTOR_MODE;
}

function reportErrorToUser(err) {
	vex.dialog.alert(err + '');
}

function getQueryParams(qs) {
	qs = qs.split('+').join(' ');
	var params = {},
	    tokens,
	    re = /[?&]?([^=]+)=([^&]*)/g;
	while (tokens = re.exec(qs)) {
		params[decodeURIComponent(tokens[1])] = decodeURIComponent(tokens[2]);
	}
	return params;
}

function fillText(className, text) {
	var spans = document.getElementsByClassName(className);
	for (var s = 0; s < spans.length; s++) {
		spans[s].innerText = text;
	}
}

function fillSrc(className, text) {
	var spans = document.getElementsByClassName(className);
	for (var s = 0; s < spans.length; s++) {
		spans[s].src = text;
	}
}

var cancels = 0;
function initWithTeamCode(_callback) {
	if (TEAM_ID) {
		_callback(TEAM_ID);
	} else {
		vex.dialog.prompt({
			message: 'Enter your team code:',
			callback: function callback(code) {
				if (code) {
					_callback(code);
				} else if (cancels >= 1) {
					window.location = './';
				} else {
					cancels++;
					initWithTeamCode(_callback);
				}
			}
		});
	}
}

